@use './rpf_design_system/spacing' as *;
@use './rpf_design_system/font-size' as *;
@use './rpf_design_system/colours' as *;

.pythonrunner-container {
  display: flex;
  flex-direction: column;
  flex: 1;
  height: 100%;
  width: 100%;
}

.pythonrunner-console {
  padding: $space-0-5 $space-1-5;
  margin: 0;
  white-space: pre-wrap;
  white-space: -moz-pre-wrap;
  white-space: -pre-wrap;
  white-space: -o-pre-wrap;
  word-wrap: break-word;
  overflow-y: auto;

  &--medium {
    @include font-size-1-5(regular);
  }

  &--large {
    @include font-size-2(regular);
  }
}

.pythonrunner-console-output-line {
  padding-right: 5px;
}

.pythonrunner-input {
  caret-color: inherit;
  color: rgb(36, 103, 236);
  display: block;
  line-height: 20px;
  padding: 2px 1px 2px 0;
}

.pythonrunner-input:focus {
  outline: 0;
}


.pythonrunner-graphic {
  overflow: auto;
  position: relative;
}

.visual-output {
  flex: 1;
  display: flex;
  min-height: 100%;
}

