.output-panel {
  display: flex;
  overflow: hidden;

  &--text {
    flex: 3;
  }

  &--visual {
    flex: 7;

    .--light & {
      border-bottom: 5px solid $rpf-grey-100;
    }

    .--dark & {
      border-bottom: 5px solid $rpf-grey-600;
    }
  }
}
